<script>
export default {

    data(){
      return { 
        billetage:false,
        indexPage:1,   
        total:0,
        totalFinal:0,
        full_screen:true,
        table_col:[
                ['Désignation','patient_num' ],
                ['Chirurgie','patient_name_and_lastname' ] ,
                ['Matérnité','patient_date_naiss' ],
                ['Nombre','patient_age' ],
                ['BRM','patient_sexe' ],
                ['BRC','patient_date_retour' ],
                ['Centre Clinique','patient_dern_visite' ], 
                ['Pédiatrie','patient_adresse' ],
                ['PF','patient_profession' ],
                ['Dispensaire','patient_note' ], 
                ['Total','patient_note' ], 
        ], 
        dataAvance_: [
          {nombre:0,billet:'20000',montant:0},
          {nombre:0,billet:'10000',montant:0},
          {nombre:0,billet:'5000',montant:0},
          {nombre:0,billet:'2000',montant:0},
          {nombre:0,billet:'1000',montant:0},
          {nombre:0,billet:'500',montant:0},
          {nombre:0,billet:'200',montant:0},
          {nombre:0,billet:'100',montant:0},
          {nombre:0,billet:'50',montant:0},
          {nombre:0,billet:'20',montant:0}, 
        ], 
      }
    },
    methods:{
        setVal(val){ 
            this.total=this.getVal(this.dataAvance_)
            return val;
        },
        getVal(val){
            var k=0;
            val.forEach(element => {
                k+=element.montant 
            });
            return k;
        },
        doneBilletage(){
            this.billetage=false;
            this.totalFinal=this.total;
        },
        setFullScreen(val){
            return !val
        },
        verifText(val,max){ 
            if(val.length>max){
                return val.substring(0,max-3)+'...'
            }
            else {
                return val;
            } 
        }
    }
}
</script>

